:root {
  /* RESPONSIVE BREAKPOINTS - Platform Standard */
  --breakpoint-mobile-max: 768px;      /* Mobile: 768px and below */
  --breakpoint-tablet-min: 769px;      /* Tablet: 769px to 1023px */
  --breakpoint-tablet-max: 1023px;     
  --breakpoint-desktop-min: 1024px;    /* Desktop: 1024px and above */
  
  /* BREAKPOINT MEDIA QUERY STRINGS */
  --media-mobile: '(max-width: 768px)';
  --media-tablet: '(min-width: 769px) and (max-width: 1023px)';
  --media-desktop: '(min-width: 1024px)';

  /* Primary Colors - Updated with light blue theme */
  --primary-100: #f9fafc;  /* Very light blue-gray */
  --primary-200: #e3e9f6;  /* Light blue (matches balance nav) */
  --primary-300: #bbd0f0;  /* Medium light blue */
  --primary-400: #90b7ea;  /* Medium blue */
  --primary-500: #3462db;  /* Main Brand Color (blue) */
  --primary-600: #2952c4;  /* Darker blue */
  --primary-700: #1e42ad;  /* Even darker blue */
  --primary-800: #1a3a7c;  /* Dark blue (matches balance nav active) */
  --primary-900: #0f2350;  /* Darkest blue */

  /* Light Blue Theme Colors for Tables and Navigation */
  --table-header-bg: linear-gradient(135deg, #f9fafc 60%, #e3e9f6 100%);
  --table-header-border: #e0e6ed;
  --table-hover-bg: #f0f4f9;
  --table-border: #e8eef5;
  --table-row-border: #e8eef5;
  --nav-bg: linear-gradient(135deg, #f9fafc 60%, #e3e9f6 100%);
  --nav-shadow: 0 4px 16px rgba(52, 98, 219, 0.06);

  /* Success/Error Light Colors for Tables */
  --success-light: #e8f5e8;
  --success-dark: #2e7d32;
  --error-light: #ffebee;
  --error-dark: #c62828;
  --warning-light: #fff8e1;
  --warning-dark: #f57c00;

  /* Navigation Height Variables */
  --nav-height-compact: 36px;      /* Compact height for smaller top bar */
  --nav-height-normal: 44px;       /* Normal height - current account menu height */
  --nav-height-large: 52px;        /* Larger height if needed */
  
  /* Navigation Padding Variables */
  --nav-padding-vertical: 8px;     /* Vertical padding for nav items */
  --nav-padding-horizontal: 16px;  /* Horizontal padding for nav items */
  --nav-padding-compact: 6px 12px; /* Compact padding for smaller nav */
  --nav-padding-normal: 8px 16px;  /* Normal padding for nav items */

  /* Balance Navigation Bar Variables */
  --balance-nav-width-mobile: 100%;       /* Full width on mobile */
  --balance-nav-width-desktop: 400px;     /* Compact width on desktop */
  --balance-nav-min-width: 300px;         /* Minimum width */
  --balance-nav-max-width-mobile: 520px;  /* Max width on mobile */
  --balance-nav-padding-desktop: 4px;     /* Compact padding on desktop */
  --balance-nav-padding-mobile: 8px;      /* Normal padding on mobile */
  --balance-nav-border-radius-desktop: 10px; /* Smaller radius on desktop */
  --balance-nav-border-radius-mobile: 14px;  /* Normal radius on mobile */
  
  /* Balance Navigation Tab Variables */
  --balance-tab-padding-desktop: 8px 16px;  /* Compact tab padding on desktop */
  --balance-tab-padding-mobile: 12px 16px;  /* Normal tab padding on mobile */
  --balance-tab-padding-mobile-small: 10px 12px; /* Small mobile padding */
  --balance-tab-font-size-desktop: 0.95rem; /* Smaller font on desktop */
  --balance-tab-font-size-mobile: 1.08rem;  /* Normal font on mobile */
  --balance-tab-font-size-mobile-small: 1rem; /* Small mobile font */
  --balance-tab-border-radius-desktop: 4px; /* Smaller tab radius on desktop */
  --balance-tab-border-radius-mobile: 6px;  /* Normal tab radius on mobile */
  --balance-tab-margin-desktop: 0 1px;      /* Tight margin on desktop */
  --balance-tab-margin-mobile: 0 2px;       /* Normal margin on mobile */

  /* Top Bar Height Variables */
  --top-bar-height: 60px;          /* Total top bar height */
  --top-bar-height-mobile: 48px;   /* Mobile top bar height */
  --top-bar-padding: 8px 24px;     /* Top bar padding */
  --top-bar-padding-mobile: 6px 16px; /* Mobile top bar padding */
  --top-bar-shadow: 0 6px 24px rgba(52, 98, 219, 0.08); /* Slightly larger shadow for top bar */

  /* Neutral Colors */
  --neutral-100: #FFFFFF;
  --neutral-200: #F5F5F5;
  --neutral-300: #E0E0E0;
  --neutral-400: #BDBDBD;
  --neutral-500: #9E9E9E;
  --neutral-600: #757575;
  --neutral-700: #616161;
  --neutral-800: #424242;
  --neutral-900: #212121;
  
  /* Text Colors */
  --text-black: #000000;          /* Pure black for titles and headings */
  --text-dark: #1a1a1a;           /* Very dark gray for better readability */
  --text-primary: #2c3e50;        /* Primary text color */
  --text-secondary: #64748b;      /* Secondary text color */

  /* Accent Colors */
  --success-color: #4CAF50;
  --warning-color: #FFC107;
  --error-color: #F44336;
  --info-color: #2196F3;

  /* Typography */
  --font-family-primary: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  --font-family-secondary: 'Poppins', sans-serif;
  --font-family-mono: 'Monaco', 'Courier New', monospace;
  
  /* Responsive Typography */
  --font-size-xs: clamp(0.75rem, 0.7rem + 0.25vw, 0.875rem);    /* 12-14px */
  --font-size-sm: clamp(0.875rem, 0.8rem + 0.375vw, 1rem);      /* 14-16px */
  --font-size-base: clamp(1rem, 0.9rem + 0.5vw, 1.125rem);      /* 16-18px */
  --font-size-lg: clamp(1.125rem, 1rem + 0.625vw, 1.25rem);     /* 18-20px */
  --font-size-xl: clamp(1.25rem, 1.1rem + 0.75vw, 1.5rem);      /* 20-24px */
  --font-size-2xl: clamp(1.5rem, 1.3rem + 1vw, 1.875rem);       /* 24-30px */
  --font-size-3xl: clamp(1.875rem, 1.6rem + 1.375vw, 2.25rem);  /* 30-36px */
  --font-size-4xl: clamp(2.25rem, 1.9rem + 1.75vw, 3rem);       /* 36-48px */

  /* Table-specific Typography */
  --table-font-size-compact: clamp(0.7rem, 0.65rem + 0.25vw, 0.8rem);     /* 11.2-12.8px - Very compact for dashboard */
  --table-font-size-normal: clamp(0.8rem, 0.75rem + 0.25vw, 0.9rem);      /* 12.8-14.4px - Normal for main pages */
  --table-font-size-header: clamp(0.65rem, 0.6rem + 0.25vw, 0.75rem);     /* 10.4-12px - Compact headers */
  --table-font-size-numeric: clamp(0.7rem, 0.65rem + 0.25vw, 0.8rem);     /* 11.2-12.8px - Numbers */

  /* Responsive Spacing */
  --spacing-xs: clamp(0.25rem, 0.2rem + 0.25vw, 0.5rem);        /* 4-8px */
  --spacing-sm: clamp(0.5rem, 0.4rem + 0.5vw, 1rem);            /* 8-16px */
  --spacing-md: clamp(1rem, 0.8rem + 1vw, 1.5rem);              /* 16-24px */
  --spacing-lg: clamp(1.5rem, 1.2rem + 1.5vw, 2rem);            /* 24-32px */
  --spacing-xl: clamp(2rem, 1.6rem + 2vw, 3rem);                /* 32-48px */
  --spacing-2xl: clamp(3rem, 2.4rem + 3vw, 4rem);               /* 48-64px */

  /* Table-specific Spacing - REDUCED for more compact desktop tables */
  --table-cell-height-compact: clamp(1.5rem, 1.4rem + 0.5vw, 1.8rem);     /* 24-28.8px - More compact for dashboard */
  --table-cell-height-normal: clamp(1.8rem, 1.6rem + 0.75vw, 2.2rem);     /* 28.8-35.2px - Reduced normal height */

  /* Border Radius */
  --border-radius-sm: 0.25rem;  /* 4px */
  --border-radius-md: 0.5rem;   /* 8px */
  --border-radius-lg: 1rem;     /* 16px */
  --border-radius-full: 9999px;

  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);

  /* Transitions */
  --transition-fast: 150ms ease-in-out;
  --transition-normal: 250ms ease-in-out;
  --transition-slow: 350ms ease-in-out;

  /* Z-index */
  --z-index-dropdown: 1000;
  --z-index-sticky: 1020;
  --z-index-fixed: 1030;
  --z-index-modal-backdrop: 1040;
  --z-index-modal: 1050;
  --z-index-popover: 1060;
  --z-index-tooltip: 1070;

  /* Responsive Layout */
  --container-padding-mobile: 1rem;
  --container-padding-tablet: 1.5rem;
  --container-padding-desktop: 2rem;
  
  --card-padding-mobile: 1rem;
  --card-padding-tablet: 1.5rem;
  --card-padding-desktop: 2rem;
  
  --table-cell-padding-mobile: 0.5rem 0.75rem;
  --table-cell-padding-tablet: 0.75rem 1rem;
  --table-cell-padding-desktop: 1rem 1.5rem;

  /* Component Widths */
  --component-width-xs: 100%;
  --component-width-sm: 100%;
  --component-width-md: 100%;
  --component-width-lg: 100%;
  --component-width-xl: 100%;
  
  /* Table Responsive */
  --table-min-width-mobile: 100%;
  --table-min-width-tablet: 100%;
  --table-min-width-desktop: 900px;
  
  --table-max-width-mobile: 100%;
  --table-max-width-tablet: 100%;
  --table-max-width-desktop: 1400px;

  --page-container-padding: 20px;
  --page-container-padding-desktop: 32px;
  
  /* Page Title Spacing */
  --page-title-margin-bottom: 12px;
  --page-title-margin-bottom-mobile: 8px;

  /* Page Title Styling Variables */
  --page-title-color: var(--text-black);                     /* Pure black for titles */
  --page-title-font-weight: 700;
  --page-title-font-size-mobile: var(--font-size-xl);        /* 1.25-1.5rem */
  --page-title-font-size-tablet: var(--font-size-2xl);       /* 1.5-1.875rem */
  --page-title-font-size-desktop: var(--font-size-3xl);      /* 1.875-2.25rem */
  --page-title-border-width: var(--spacing-xs);              /* 4-8px border width */
  --page-title-border-color: var(--info-color);              /* Blue accent */
  --page-title-padding-left: var(--spacing-xs);              /* Left padding */

  /* Chart Responsive Sizing */
  --chart-container-height-mobile: 400px;       /* Mobile chart container height */
  --chart-container-height-tablet: 500px;       /* Tablet chart container height */
  --chart-container-height-desktop: 600px;      /* Desktop chart container height */
  --pie-chart-radius-mobile: 120px;             /* Mobile pie chart radius */
  --pie-chart-radius-tablet: 160px;             /* Tablet pie chart radius */
  --pie-chart-radius-desktop: 200px;            /* Desktop pie chart radius */
  --chart-min-height-mobile: 300px;             /* Minimum chart height on mobile */
  --chart-min-height-desktop: 400px;            /* Minimum chart height on desktop */
} 